html{
    width: 1920px fixed;
    height: 1080px fixed;
}

#root{
    width: 100%;
    height: 100%;
}


.footer{
    height: 70px;
    margin-inline: center;
}
.bgcolor{
    margin-block: 25px;
}

#bgcolor{
    font-size: 24px;
    text-align: center;
    background-color: inherit;
}

#para{
    color: gold;
    display: flex;
    font-size: 20px;
    width: 600px fixed;
    height: 300px fixed;
    min-width: 600px;
    min-height: 300px;
    background-color: inherit;
    border-collapse: collapse;
    text-justify: distribute-all-lines;
}

#para::placeholder{
    color: red;
    word-wrap: break-word;
}




